import React from "react";
import { useEffect } from "react";

const Slide = (props) => {
  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://res.cloudinary.com/cockbook/raw/upload/v1658509341/single/a_kvrhmg.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);
  return (
    <div
      className="jdifgpjfrklsjdksdjasd"
      style={{
        width: "100%",
        height: "40%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div className="slider-pro" id="slide">
        <div className="sp-slides">
          <div className="sp-slide">
            <img
              alt="open"
              className="sp-image"
              src="https://res.cloudinary.com/cockbook/image/upload/v1657958630/single/week-streak-badge.fd8c50f24ce2a57_pce8vw.jpg"
            />
          </div>
          <div className="sp-slide">
            <img
              alt="open"
              className="sp-image"
              src="https://res.cloudinary.com/cockbook/image/upload/v1657958630/single/week-streak-badge.fd8c50f24ce2a57_pce8vw.jpg"
            />
          </div>
          <div className="sp-slide">
            <img
              alt="open"
              className="sp-image"
              src="https://res.cloudinary.com/cockbook/image/upload/v1657958630/single/week-streak-badge.fd8c50f24ce2a57_pce8vw.jpg"
            />
          </div>
          <div className="sp-slide">
            <img
              alt="open"
              className="sp-image"
              src="https://res.cloudinary.com/cockbook/image/upload/v1657958630/single/week-streak-badge.fd8c50f24ce2a57_pce8vw.jpg"
            />
          </div>
          <div className="sp-slide">
            <img
              alt="open"
              className="sp-image"
              src="https://res.cloudinary.com/cockbook/image/upload/v1657958630/single/week-streak-badge.fd8c50f24ce2a57_pce8vw.jpg"
            />
          </div>
        </div>
        <div className="sp-thumbnails">
          <div className="sp-thumbnail">1  </div>
          <div className="sp-thumbnail">2  </div>
          <div className="sp-thumbnail">3  </div>
          <div className="sp-thumbnail">4  </div>
          <div className="sp-thumbnail">5  </div>
        </div>
      </div>
    </div>
  );
};

export default Slide;
